import React from 'react'
import PropTypes from 'prop-types'


export default function Navbar(props) {
 
    return (
        <nav className={`navbar navbar-expand-lg navbar-${props.mode} bg-${props.mode}`}>
        <div className="container-fluid">
          <a className="navbar-brand" href="/"> {props.title}</a>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <a className="nav-link active" aria-current="page" href="/">Home</a>
              </li>
              <li className="nav-item">
                <a className="nav-link" href="./">{props.aboutText}</a>
              </li>
            </ul>
            <div className={`form-check form-switch text-${props.mode==='light'?'dark':'light'} `}>
            <input className="form-check-input" onClick={props.toggleMode} id="flexSwitchCheckDefault" type="checkbox" />
            <label className="form-check-lable" htmlFor="flexSwitchCheckDefault">Enable Darkmode</label>
            </div>
          </div>
        </div>
      </nav>
    )
}
// yha ham prop ke type ko declare karte hai ki iska title ka prop type kya hoga
// and isRequired means ki if hum by chance ham bhul jate hai title declare karna to hume error show hoga
Navbar.propTypes = {
    title: PropTypes.string.isRequired,
    aboutText:PropTypes.string.isRequired,
}
// if we do'nt pass the props manually so the default props are execute automatically
Navbar.defaultProps = {
    title: 'set title here',
    aboutText: 'default text'
}